<template lang="pug">
  div
    v-flex.my-4(row wrap)
      img(
        :src="logo"
        class="hidden-xs-only"
      )
      h1(class="white--text") Katie Marriner Photography
    v-flex(row)
      v-btn.mx-0(flat class="white--text" href="https://www.instagram.com/katiemarriner/") Instagram
      v-btn.mx-0(flat class="white--text" href="https://www.flickr.com/photos/katiemarriner/") Flickr
    v-flex(row)
      div#email-text(class="white--text") info@katiemarriner.com
</template>

<script>
import logo from './../../static/logo.svg';

export default {
  name: 'CustomHeader',
  data() {
    return {
      logo,
      snackbar: false,
      y: 'top',
      x: null,
      mode: '',
      timeout: 6000,
    };
  },
  methods: {
    copyText(e){
      const copyText = document.getElementById('email-text');
      
      copyText.select();
      document.execCommand("Copy");
      
      this.snackbar = true;
    }
  }
}
</script>

<style>
.hidden {
  visibility: hidden;
  height: 0px;
}
</style>



