<template lang="pug">
  v-container(wrap class="black")
    div(row wrap :class="photos")
      div(v-for="post in posts.all" :key="post.slug")
        image-homepage(:imageObj="post")
</template>

<script>
import axios from 'axios';

import config from './../../keys.json';
import ImageHomepage from './ImageHomepage.vue';

export default {
  name: 'Homepage',
  data() {
    return {
      posts: {
        all: [],
      },
      params: {
        method: 'flickr.photosets.getPhotos',
        photoset_id: '72157694261798354',
        w: null,
      },
      photos: 'photos--sm',
    };
  },
  watch: {
    'params.w'() {
      this.photos = this.getClass();
    },
  },
  computed: {
    requestURL() {
      return `https://api.flickr.com/services/rest/?method=${this.params.method}&photoset_id=${this.params.photoset_id}&format=json&api_key=${config.flickr_api_key}&nojsoncallback=1`;
    },
  },
  mounted() {
    axios.get(this.requestURL).then((res) => {
      this.posts.all = res.data.photoset.photo;
    });

    this.checkWidth();

    window.onresize = this.checkWidth();
  },
  methods: {
    checkWidth() {
      this.params.w = window.innerWidth;
    },
    getClass() {
      if (this.params.w <= 450) {
        return 'photos--xs';
      }
      return 'photos--sm';
    },
  },
  components: {
    ImageHomepage,
  },
};
</script>

<style>
.photos--xs {
  line-height: 0;
  -webkit-column-count: 1;
  -webkit-column-gap:   0px;
  -moz-column-count:    1;
  -moz-column-gap:      0px;
  column-count:         1;
  column-gap:           0px;
}
.photos--sm {
  line-height: 0;
  -webkit-column-count: 2;
  -webkit-column-gap:   0px;
  -moz-column-count:    2;
  -moz-column-gap:      0px;
  column-count:         2;
  column-gap:           0px;
}
</style>



