<template lang="pug">
  div.homepage__preview-post
    img.homepage__preview-image(:src="imageURL")
</template>

<script>
export default {
  name: 'ImageHomepage',
  props: ['imageObj'],
  data() {
    return {

    };
  },
  computed: {
    imageURL() {
      return `https://farm${this.imageObj.farm}.staticflickr.com/${this.imageObj.server}/${this.imageObj.id}_${this.imageObj.secret}.jpg`
    },
  },
};
</script>

<style>
.homepage__preview-post {
  padding: 15px;
}
.homepage__preview-image {
  /* Just in case there are inline attributes */
  width: 100% !important;
  height: auto !important;
}
</style>



