<template lang="pug">
  div
    h2(class="white--text") Katie Marriner Photography
    p.pt-3(class="grey--text") Copyright © {{ year }} Katie Marriner
    v-btn(flat class="white--text" href="https://www.instagram.com/katiemarriner/") Instagram
    v-btn(flat class="white--text" href="https://www.flickr.com/photos/katiemarriner/") Flickr
    p(class="white--text") Email info@katiemarriner.com
</template>

<script>
export default {
  name: 'CustomFooter',
  computed: {
    year() {
      const d = new Date();
      return d.getFullYear();
    },
  },
};
</script>




